body {
  font-family: Arial, Helvetica, sans-serif;
  max-width: 650px;
  margin: 0 auto;
  padding: 0 16px;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #202123 !important;
  color: #212529
}

.main-content {
  flex-grow: 1
}

footer {
  color: #888888;
  border-top: 1px solid #eeeeee;
  margin-top: 16px;
  padding: 16px 0;
}

header {
  margin-top: 16px;
  margin-bottom: 48px;
}


h1 {
  font-size: 64px;
  margin-bottom: 16px;
  color: white;
}

h2 {
  font-size: 32px;
  margin: 16px;
  color: white;
}

header a {
  color: #888888;
  margin-right: 16px;
  text-decoration: none;
}


button{
  cursor: pointer;
  border: 1px solid #888888;
  background: #888888;
  color: white;
  padding: 8px;
  justify-content: center;
}

.search-content {
  display: table
}

.weather-info{
  padding-top: 16px;
}

input{
  border: 1px solid #cccccc;
  padding: 8px;
  justify-content: center;
  display: table-cell;
  width: 100%;
  
}

